* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #fa5805;
  --primary-color-darker: #e95003;
  --secondary-color: #f6f6f6;
  --secondary-color-darker: #e6e6e6;

  --third-color: #b7bcc4; 

  --text-color: #222222;
  --text-color2: #ffffff;
  --text-color3: #717989;

  --font1: "Noto Sans KR", sans-serif;
  --font2: "Montserrat", sans-serif;
}

html {
  scroll-behavior: smooth;
}
html,
body {
  width: 100%;
  height: 100%;
  /* overflow-x: hidden; */
}
.btn {
  display: inline-block;

  padding: 20px 70px;
  border-radius: 10px;

  font-size: 18px;
  font-family: "Noto Sans KR", sans-serif;
  font-family: var(--font1);
  font-weight: 300;

  transition: all 0.3s ease-in;
}

section {
  padding: 80px 0;
}

.section__header h5 {
  margin-bottom: 16px;

  font-family: "Montserrat", sans-serif;

  font-family: var(--font2);
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
}
.section__header h2 {
  margin-bottom: 30px;

  font-size: 35px;
  font-weight: 600;
}
.section__header p {
  margin-bottom: 30px;

  font-size: 20px;
  font-weight: 300;
  line-height: 30px;
}

/* TABLET - ipad: 768*/
@media (min-width: 740px) and (max-width: 1023px) {
  .wide {
    max-width: 1023px;
    min-width: 740px;
  }
  .row {
    margin-left: 16px;
    margin-right: 16px;
  }
  .btn{
    padding: 16px 55px;
  }
}

/* MOBILE - iphone 6/7/8 plus: 414 */
@media (max-width: 740px) {
  .wide {
    max-width: 740px;
  }
  .row {
    margin-left: 8px;
    margin-right: 8px;
  }
  .btn {
    padding: 15px 40px;
    border-radius: 8px;

    font-size: 16px;
  }

  .section__header {
    text-align: center;
    width: 100%;
  }
  .section__header h2 {
      font-size: 27px;
      margin-bottom: 22px;
  }
  .section__header p {
    font-size: 15px;
    line-height: 24px;
}
}


.header {
    position: fixed;
    z-index: 99;
    
    display: flex;
    align-items: center;
    justify-content: center;
    
    height: 65px;
    width: 100%;
    
    transition: all 0.3s ease;
}
.header.active {
    background-color: var(--text-color);
    height: 50px;
}

.align-item-center {
    display: flex;
    align-items: center;
}
.header__logo img{
    width: 100px;
}
.header__menu {
    display: flex;
    justify-content: flex-end;
}
.header_btn {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 80px;
    height: 32px;
    border-radius: 6px;
    margin-left: 10px;

    color: var(--secondary-color);

    font-size: 12px;
    font-family: var(--font2);

    transition: all 0.3s ease-in;
}
.header_btn:hover {
    font-weight: 600;
}
.btn-login {
    border: 1px solid var(--secondary-color);
}
.btn-login:hover{
    background-color: var(--secondary-color);
    color: var(--text-color);
}
.btn-join {
    border: 1px solid var(--primary-color);
    background-color: var(--primary-color);
}
.btn-join:hover {
    background-color: var(--primary-color-darker);
    color: var(--secondary-color);
}

@media (max-width: 1023px) {
    .header{
        width: 100%;
    }

}

