.work{
    background-color: var(--secondary-color);
}

.work__header h5{
    color: var(--primary-color)

}
.work__header h2{
    color: var(--text-color);

}
.work__header p{
    color: var(--text-color);
    opacity: 0.8;
}

.work__block{
    width: 100%;
    height: 533px;

    overflow: hidden;
    position: relative;
}
.block__content {
    display: flex;
    justify-content: space-between;

    position: absolute;
    top: 0;
    left: 0;
    
    width: 100%;
    height: 100%;
    padding: 32px;

    transition: all 0.3s ease;
}
.block__content:hover {
  background-color:  rgba(0, 0, 0, 0.342);
}
.block__img{
    width: 100%;
    height: 100%;
}
.block__img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.block__text{
    width: 77%;

}
.block__text p {
    font-size: 16px;
    font-family: var(--font1);
    margin-bottom: 20px;
    font-weight: 300;
    color: var(--text-color);

    transition: all 0.3s ease;
}

.block__text h2 {
    font-size: 30px;
    font-weight: 600;
    line-height: 40px;
    color: var(--text-color);

    transition: all 0.3s ease;
}

.block__content:hover .block__text p ,
.block__content:hover .block__text h2 {
    color: var(--text-color2);
}
.block__arrow  img{
    transition: all 0.3s ease;
}
.block__arrow:hover img {
    -webkit-transform: translateX(15%);
            transform: translateX(15%);
}

.lg-4{
    width: 33%;
}
.md-4 {
    width: 32%;
}
.row.justify-content-between {
    justify-content: space-between;
