.meeting .grid {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    
    position: relative;
}
.meeting .grid::before {
    content: '';
    width: 0%;
    height: 0%;

    position: absolute;
    top: 0;
    left: 0;

    background-color: #00000034;
    transition: all 0.3s ease;
}
.meeting .grid::after {
    content: '';
    width: 0%;
    height: 0%;

    position: absolute;
    bottom: 0;
    right: 0;

    background-color: #00000034;
    transition: all 0.3s ease;
}

.meeting:hover .grid::before,
.meeting:hover .grid::after {
    width: 100%;
    height: 100%;
}

